import { supabase } from '../supabase';

//  Supabase 
export const getBooksFromSupabase = async (user_id) => {
  console.time("getBooksFromSupabase");

  const { data, error } = await supabase
    .from("books")
    .select("*")
    .eq("user_id", user_id);

  if (error) {
    console.error("Supabase :", error.message);
    console.timeEnd("getBooksFromSupabase");
    return [];
  }

  console.timeEnd("getBooksFromSupabase");
  console.log("Supabase :", data);
  return data;
};

//  Supabase Storage 
export const uploadToSupabase = async (book, user_id) => {
  console.time("uploadToSupabase");

  try {
    // 
    // 
    const slugify = (str) => str.normalize("NFKD")
        .replace(/[\u0300-\u036f]/g, "") // 
        .replace(/[^\w\s-]/g, "")        // 
        .trim()
        .replace(/\s+/g, "-")            //  -
        .toLowerCase();

    const fileName = `${slugify(book.name)}-${Date.now()}.pdf`; //  .pdf

    //  Base64  Uint8Array
    const base64Data = book.data.split(",")[1]; //  "data:application/pdf;base64,"
    const binaryData = Uint8Array.from(atob(base64Data), c => c.charCodeAt(0));


    const file = new File([binaryData], fileName, { type: "application/pdf" });


    //  Storage bucketbooks/user_id/file.pdf
    const { error: storageError } = await supabase
      .storage
      .from("books")
      .upload(`${user_id}/${book.id}.pdf`, file, { upsert: true });

    if (storageError) {
      console.error("Storage :", storageError.message);
      return;
    }

    
    //  URL
    const { data: urlData } = supabase
      .storage
      .from("books")
      .getPublicUrl(`${user_id}/${book.id}.pdf`);

    const fileUrl = urlData.publicUrl;

    //  metadata 
    const { error: insertError } = await supabase.from("books").insert([{
      id: book.id,
      name: book.name,
      user_id,
      file_url: fileUrl,
    }]);

    if (insertError) {
      console.error("Supabase  metadata :", insertError.message);
    } else {
      console.log(`  ${book.name} `);
    }
    
    return fileUrl;

  } catch (err) {
    console.error(" :", err);
  }

  console.timeEnd("uploadToSupabase");
};

// Storage + Metadata
export const deleteFromSupabase = async (bookName, user_id) => {
  console.time("deleteFromSupabase");

  try {
    // 
    const { data: books, error: fetchError } = await supabase
      .from("books")
      .select("*")
      .eq("user_id", user_id)
      .eq("name", bookName);

    if (fetchError || !books.length) {
      console.error(":", fetchError?.message);
      return;
    }

    const path = books[0].file_url.split('/storage/v1/object/public/books/')[1];

    //  Storage 
    const { error: storageError } = await supabase
      .storage
      .from("books")
      .remove([path]);

    if (storageError) {
      console.error("Storage :", storageError.message);
    }

    //  metadata
    const { error: deleteError } = await supabase
      .from("books")
      .delete()
      .eq("user_id", user_id)
      .eq("name", bookName);

    if (deleteError) {
      console.error(":", deleteError.message);
    } else {
      console.log(`  ${bookName} `);
    }

  } catch (err) {
    console.error(" :", err);
  }

  console.timeEnd("deleteFromSupabase");
};

//  Storage
export const viewBookContent = async (bookName, user_id) => {
  console.time("viewBookContent");

  try {
    // 
    const { data: books, error: fetchError } = await supabase
      .from("books")
      .select("*")
      .eq("user_id", user_id)
      .eq("name", bookName);

    if (fetchError || !books.length) {
      console.error(":", fetchError?.message);
      return null;
    }

    //  URL
    const fileUrl = books[0].file_url;

    //  text 
    const response = await fetch(fileUrl);
    const textContent = await response.text();

    console.timeEnd("viewBookContent");
    return textContent;

  } catch (err) {
    console.error(" :", err);
    console.timeEnd("viewBookContent");
    return null;
  }
};
